.contact {
  &__content {
    @include pageGrid;
  }

  &__form {
    grid-column: 1 / -1;

    @include onTablet {
      min-width: 288px;
      grid-column: 1 / 3;
    }

    @include onDesktop {
      min-width: 411px;
      grid-column: 1 / 5;
    }
  }

  &__send {
    @include onTablet {
      grid-column: 4 / 6;
    }

    @include onDesktop {
      grid-column: 7 / 10;
    }
  }

  &__info {
    margin-bottom: 24px;

    &-subtitle {
      font-weight: 400;
      line-height: 20px;
      font-size: 14px;
      margin-bottom: 8px;
    }

    &-des {
      font-weight: 500;
      font-size: 16px;
      line-height: 23px;
    }
  }

  &__input {
    border-radius: 40px;
    height: 48px;
    width: 100%;
    box-sizing: border-box;
    font-size: 14px;
    color: #292929;
    margin: 0 0 16px;
    padding: 0 16px;

    @include onTablet {

      &:hover {
        &::placeholder {
          color: #292929;
        }
      }
    }
  }

  &__textarea {
    box-sizing: border-box;
    border-radius: 16px;
    padding: 14px 16px;
    font-size: 14px;
    font-weight: 400;
    height: 148px;
    width: 100%;
    margin-bottom: 32px;
    line-height: 20px;
    resize: none;
    outline: none;

    @include onTablet {
      &:hover {
        &::placeholder {
          color: #292929;
        }
      }
    }
  }

  &__link {
    text-decoration: none;
    color: $main-color-text;
    transition: color 0.3s;

    @include onDesktop {
      &:hover {
        color: #7e7e83;
      }
    }

    @include onTablet {
      &:hover {
        color: #7e7e83;
      }
    }
  }
}
